.App-header {
  background-color: #282c34;
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  color: white;
}

.App-header h1 {
  margin: 0;
  margin-left: 10px;
  font-size: 1.5em;
  cursor: pointer;
}

.header-button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.header-button:hover {
  background-color: #45a049;
}

@keyframes shake {
  0%, 80%, 100% { transform: translateX(0); }
  83% { transform: translateX(-8px); }
  86% { transform: translateX(8px); }
  89% { transform: translateX(-8px); }
  92% { transform: translateX(8px); }
  95% { transform: translateX(-8px); }
}

.header-button {
  animation: shake 3s ease-in-out infinite;
}

.header-button:hover {
  animation: none;
}
