.quiz-container {
  position: relative;
  max-width: 500px;
  margin: 40px auto 0;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.question-section {
  margin-bottom: 20px;
}

.question-count {
  margin-bottom: 10px;
  font-weight: bold;
}

.question-text {
  font-size: 18px;
  margin-bottom: 20px;
}

.answer-section {
  display: flex;
  flex-direction: column;
}

.answer-section button {
  margin-bottom: 10px;
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.answer-section button:hover {
  background-color: #45a049;
}

.score-section {
  text-align: center;
  font-size: 24px;
}

.score-section button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.score-section button:hover {
  background-color: #45a049;
}

.home-button {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.home-button:hover {
  background-color: #45a049;
}
